// Sidebar ---*/
.sidebar {
	.widget {
		.thumb {
			.not-thumb {
				background-color: #f0f0f0;
				&:before {
					color: #ccc;
				}
			}
		} // .thumb
	} // .widget
	.widgettitle {
		border-bottom: 1px solid #ddd;
	}
	.widget_newpost,
	.widget_popular {
		time {
			font-family: $font-google;
			color: #454545;
		}
		.title {
			color: #454545;
			// color: $primary-color;
		}
	} // widget_newpost,widget_popular
	.widget_search {
		input[type="text"] {
			background-color: #ddd;
			&:focus {
				color: #454545;
			}
		}
		button[type="submit"],
		input[type="submit"] {
			color: #fff;
			background-color: #74bba2;
		}
	} // .widget_search
	.widget_archive {
		li {
			a {
				// color: $primary-color;
			}
			.tgl {
				// color: $primary-color;
				i {
					color: #888;
				}
			}
		}
	}
	
	.tagcloud {
		& > a {
			color: #888;
			background-color: #fcfcfc;
			border: 1px solid #aaa;
		}
	}
}
