// Staff Archives ---*/
.staff-loop {
	.item {
		article {
			background-color: #fff;
		}
	}
	.manage {
		color: #1a1a1a;
	}
	.staff-name {
		a {
			color: #1a1a1a;
		}
	}
}
@media (max-width: $screen-tab - 1) {
	.staff-loop {
		.staff-name {
			// background-color: rgba(#fff, .6);
		}
	}
}
@media (min-width: $screen-tab) {
	.staff-loop {
		.staff-name {
			// background: rgba(#fff, .6);
		}
	}
}

// Staff Single ---*/
.staff-header {
	.manage {
		& > span {
			color: #aaa;
			border: 1px solid #aaa;
		}
	}
}
.staff-table {
	th,td {
		border-bottom: 1px solid #ddd;
	}
	th {
		color: #888;
	}
}
.staff-comment {
	color: #666;
	background-color: #f6f6f6;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_info.png'));
}
@media (min-width: $screen-tab) {
	.staff-header {
		.title {
			border-bottom: 1px solid #ddd;
		}
	}
}

// Other Staff ---*/
.other-staff {
	background-color: #eee;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_bread.png'));
	.title {
		color: #1a1a1a;
		.en {
			line-height: 1;
			font-family: $font-google;
			font-size: 28px;
		}
	}
	.btn-default {
		background-color: #aaa;
		border-color: #aaa;
	}
}
.other-staff-list {
	.item {
		color: #888;
	}
	.manage {
		background-color: #fcfcfc;
		border: 1px solid #dedede;
	}
	.staff-list-next,
	.staff-list-prev {
		color: #fff;
		background: #aaa;
	}
}
