.post-archive {
	border-bottom: 1px solid #ddd;
	.title {
		a {
			color: #1a1a1a;
		}
	}
	.not-thumb {
		background: #fcfcfc;
	}
	.cat {
		color: #888;
		background-color: #fcfcfc;
		border: 1px solid #aaa;
		a {
			color: #888;
		}
	}
	.datetime {
		font-family: $font-google;
		font-size: 20px;
		color: #1a1a1a;
	}
	.content {
		color: #1a1a1a;
		&:after {
			background: none;
		}
	}
	@media (max-width: $screen-tab - 1) {
		background-color: #fff;
		box-shadow: 0 1px 2px 1px rgba(#454545, .2);
		.post-link {
			&:hover {
				background-color: #fcfcfc;
			}
		}
		.title {
			&:after {
				@include sgradient(
					$colors:(
						(rgba(#fff, 0), 0%),
						(rgba(#fff, 0), 50%),
						(rgba(#fff, 0), 51%),
						(rgba(#fff, 1), 100%)
					)
				);
			}
		}
	}
}

.post-single {
	.post-header {
		// border-bottom: 1px solid #ddd;
	}
	
	.datetime {
		font-family: $font-google;
		font-size: 24px;
		color: #1a1a1a;
	}
	.cat {
		color: #888;
		a {
			font-size: 14px;
			color: #888;
			background-color: transparent;
			border: 1px solid #aaa;
		}
	}
	@media (min-width: $screen-tab) {
		.datetime {
			font-size: 24px;
		}
	}
}
.social-count {
	.fa-facebook, .fa-twitter {
		color: #aaa;
	}
	.count {
		color: #aaa;
	}
}

.single-pager {
	border-top: 1px solid #ccc;
	a {
		color: #fff;
		background-color: #aaa;
		border: 1px solid #aaa;
	}
}
