.menu-title {
	border-bottom: 1px solid #ddd;
	.en {
		font-size: 32px;
		color: #1a1a1a;
		&:before {
			content: url(dir('common/tlt_before.png'));
			display: inline-block;
			margin-right: 10px;
			vertical-align: middle;
		}
	}
	.jp {
		color: #888;
	}
}
.menu-container {
	.menu-flex {
		border-bottom: 1px solid #f2f2f2;
	}
	.menu-etc-text {
		color: #555;
	}
	.menu-time {
		color: #888;
		background-color: #fcfcfc;
	}
	.menu-time-num {
		border: 1px solid #aaa;
	}
	.menu-price {
		color: #888;
	}
	.menu-other-text {
		color: #666;
		background-color: #f6f6f6;
		background-repeat: repeat;
		background-position: center;
		background-image: url(dir('common/bg_info.png'));
	}
}
