.salon-name {
	border-bottom: 1px solid #ddd;
	.en {
		color: #1a1a1a;
	}
	.jp {
		color: #888;
	}
}

.salon-info-table {
	th {
		color: #888;
	}
	th, td {
		border-bottom: 1px solid #ddd;
	}
}

.salon-photo-map {
	.item {
		background-color: #fcfcfc;
	}
	.image-map {
		border: 1px solid #ddd;
	}
}

.salon-access-flex {
	border-bottom: 1px solid #ddd;
}
.salon-access-title {
	.en {
		font-size: 44px;
		line-height: 1;
	}
}

.salon-access-route {
	color: #454545;
	background-color: #f6f6f6;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_info.png'));
}
