// パンくずリスト ---*/
.breadcrumb {
	background-color: #f6f6f6;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_bread.png'));
	border: none;
}

.bread-list {
	& > li {
		a {
			color: #7abfa6;
		}

		&:before {
			content: "\f105";
			position: absolute;
			left: 20px;
			line-height: 1;
			font-family: "FontAwesome";
			font-size: 16px;
			color: #444;
		}
		&:first-child {
			&:before {
				display: none;
			}
		}
	}
}

// Header ---*/
.header-top {
	overflow: hidden;
}
.site-title {
	font-size: 10px;
	color: $header-title-color;
}
.site-logo {
	a {
		&:hover {
			opacity: .8;
		}
	}
}
@media (max-width: $screen-sp) {
	.site-title {
		background-color: $header-title-bg-color;
	}
}
@media (min-width: $screen-tab) {
	.site-title {
		font-size: 11px;
	}
}

.subnavi {
	a {
		font-size: 14px;
		color: $header-subnavi-color;
		// background-color: $header-subnavi-bg-color;
		background-color: transparent;
		// border: 1px solid $header-subnavi-border-color;
		border: none;
		span {
			display: inline;
		}
		&:hover {
			color: darken($header-subnavi-color, 15%);
			text-decoration: underline;
		}
	}
	i {
		font-size: 126%;
		color: $header-subnavi-icon-color;
	}
}

// Header Contact ---*/
.header-contact {
	.btn-default {
		color: #767676;
		background-color: #f1f1f1;
		border: 1px solid #f1f1f1;
		box-shadow: inset 0 0 1px 4px #ededed;
	}
}

// #spContact ---*/
.contact-sp {
	.contact {
		background-color: $contact-sp-bg-color;
		& > li {
			&:nth-child(2) {
				border-left: 1px solid $contact-sp-border-color;
			}
			& > a {
				color: $contact-sp-color;
			}
		}
	}
}

// Footer ---*/
#footer {
	color: #555;
	background-color: #b9d5cd;
	.footer-link {
		a {
			color: #555;
		}
		.btn-default {
			color: #767676;
			background-color: #f1f1f1;
			box-shadow: inset 0 0 1px 4px #ededed;
		}
	}
}
.footer-navi-wrap {
	background-color: #fff;
	.foot-navi {
		& > li {
			a {
				color: #89b6a9;
				&:hover {
					border-bottom: 1px dotted #b9d5cd;
				}
			}
		}
	}

}
// Footer Social Button ---*/
.footer-social-button-wrap {
	background-color: #fff;
	border-top: 1px solid $footer-social-btn-border-color;
	border-bottom: 1px solid $footer-social-btn-border-color;
	.ico {
		color: #fff;
	}
	.pagetop {
		&.btn-shark {
			background: rgba(#89b6a9, .9);
			border: 1px solid transparent;
			&:hover {
				opacity: .8;
			}
		}
		.fa {
			color: $footer-social-btn-icon-color;
		}
	}
}


// Footer Contact ---*/
.footer-contact-wrap {
	background-color: transparent;
	// background-color: $footer-contact-bg-color;
	border-bottom: 1px solid $footer-contact-border-color;
}

// Footer Navi ---*/
.copyright {
	color: $copyright-color;
}



