.en {
	font-family: $font-google;
}
.c-title {
	margin-bottom: 24px;
	padding: 10px 0;
	min-height: 75px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	background-image: url(dir('common/tlt_bg.png'));
	@media (max-width: $screen-tab - 1) {
		margin-left: -15px;
		margin-right: -15px;
	}
	.en {
		// padding-left: 40px;
		// padding-right: 40px;
		margin: 0;
		margin-top: -36px;
		font-weight: 700;
		font-size: 36px;
		text-shadow: 0 1px 0 #fff,
								0 -1px 0 #fff,
								1px 0 0 #fff,
								-1px 0 0 #fff,
								1px 1px 0 #fff,
								-1px -1px 0 #fff,
								1px -1px 0 #fff,
								-1px 1px 0 #fff,
								2px 0 0 #fff,
								-2px 0 0 #fff,
								0 2px 0 #fff,
								0 -2px 0 #fff;
		filter: drop-shadow(0 0 1px rgba(#fff, 1));
		&:before, &:after {
			content: "";
			display: none;
		}
	}
	.jp {
		
	}
}
.c-title-sm {
	.en {
		font-size: 28px;
	}
}
.c-title-page {
	margin-top: 0;
	margin-bottom: 32px;
	&:after {
		content: url(dir('common/tlt_page_bg.png'));
		display: block;
		margin-top: 20px;
	}
	.en {
		position: relative;
		// padding-left: 24px;
		// padding-right: 24px;
		font-size: 42px;
		&:before, &:after {
			content: "";
			display: none;
		}
	}
	.jp {
		font-size: 14px;
		color: #644f44;
	}
	@media (min-width: $screen-tab) {
		.en {
			padding-left: 40px;
			padding-right: 40px;
			font-size: 46px;
			&:before, &:after {
				width: 68px;
				height: 30px;
			}
		}
	}
}
