$drawer-width: 220px;
// Drawer Navigation ---*/
@media (max-width: $screen-tab) {
	#drawerOverlay {
		background-color: rgba(#222, .7);
	}
}

#drawer {
	background-color: #fff;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg.png'));
	.drawer-content-back {
		& > header {
			background-color: #fff;
			background-image: none;
		}
	}
}


// ドロワーメニュー用のボタン
.drawer {
	&-btn {
		color: #fff;
		text-shadow: 0 -1px 1px rgba(darken(#b7d3cb, 5%), .8);
		background-color: rgba(#b7d3cb, 1);
		border: 1px solid darken(#b7d3cb, 2%);
		&:hover {
			background-color: rgba(darken(#b7d3cb, 5%), .7);
		}
	}
	&-btn-close {
		background-color: darken($navi-color-mode, 2%);
		border-left-color: $navi-color-mode;
		&:hover {
			background-color: #922;
		}
	}
}
// ドロワーメニュー
.drawer {
	&-navi {
		background-color: #fcfcfc;
		&:before {
			background-color: #fcfcfc;
		}
		.logo-area {
			font-family: 'Amatic SC', sans-serif;
			color: #fff;
			@include sgradient(
				$colors: (
					(lighten($navi-color-mode, 2%), 0%),
					(darken($navi-color-mode, 2%), 100%)
				)
			);
			border-bottom: 1px solid #999;
		}
	}
	&-inner {
		background-color: #f4f1ec;
	}
}
.drawer-gnavi {
	& > li {
		&.current-menu-item {
			color: #fff;
			background-color: #b7d3cb;
			> a {
        color: #fff;

        > .ja {
          color: #fff;
        }
        > .en {
          color: #fff;
        }
			}
			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				display: block;
				width: 0;
				height: 100%;
				border-left: 4px solid $navi-color-mode;
			}
		}
		& > a {
			color: $navi-color-jp;
      border-bottom: 1px solid #ccc;
      > .ja {
        color: $navi-color-jp;
      }
      > .en {
        font-family: $font-google;
				font-size: 24px;
				color: $navi-color-en;
      }
			&.hover {
				color: #e6e6e6;
        background-color: #222;
        > .ja {
          color: #e6e6e6;
        }
        > .en {
          color: #e6e6e6;
        }
			}
		}
	}
} // ul.drawer-gnavi

.drawer-subnavi {
	& > li {
		border-bottom: 1px solid lighten(#b7d3cb, 2%);
		& > a {
			color: #fff;
			background-color: #b7d3cb;
			&:hover {
				background-color: #b7d3cb;
			}
		}
	}
}

// グローバルナビゲーション ---*/
.gnavi-block {
	margin-bottom: 0;
}
.gnavi {
	& > li {
		a {
			padding: 2px 0 10px;
			color: $navi-color-mode;
		}
		&:last-child {
			& > a {
				&:after {
					border-right: 0;
				}
			}
		}
		&:before {
			content: url(dir('common/navi_before.png'));
			display: block;
		}
		&.current-menu-item, &:hover {
			&:after {
				content: "";
				position: absolute;
				// top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				display: block;
				width: 68%;
				height: 0;
				margin-left: auto;
				margin-right: auto;
				border-bottom: 2px solid $navi-color-mode;
			}
		}
		& > a {
      color: $navi-color-jp;
      > .ja {
        color: $navi-color-jp;
      }
      > .en {
        font-family: 'Josefin Slab', serif;
				font-size: 20px;
				color: $navi-color-en;
      }
			&:hover {
				background-color: transparent;
			}
			&:after {
				border-left: 1px solid $navi-color-line;
			}
		}
	}
}
