// .front-info ---*/
.front-info {
	background-color: #fff;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_info.png'));
	.c-title-sm {
		.en {
			font-size: 32px;
		}
		&:after {
			content: url(dir('common/img_info_bottom.png'));
			display: block;
			margin-top: 14px;
		}
	}
	.item {
		a {
			color: #1a1a1a;
		}
	}

}

// Concept ---*/
.concept {
	& > div {
		position: relative;
		z-index: 1;
	}
	&:before, &:after {
		content: "";
		position: absolute;
		top: 32px;
		z-index: 0;
		display: block;
		height: 0;
		background-color: transparent;
		background-repeat: no-repeat;
		background-size: contain;
		@media (min-width: $screen-tab) {
			max-width: 264px;
			width: 100%;
			height: 0;
			padding-bottom: 13%;
		}
	}
}

// Freespace ---*/
.freespace-wrap {
	background-color: #b9d5cd;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_free.png'));
}
.freespace {
	// box-shadow: 0 3px 5px 0 rgba(#000, .2);
	img {
		margin-bottom: 10px;
		border: 4px solid #fff;
		border-radius: 15px;
	}
	figcaption {
		position: relative;
		color: #1a1a1a;
		background-color: transparent;
		// background-color: rgba(#6b5345, .7);
	}
}

// Blog ---*/
.front-blog {
	.thumb {
		color: #7abfa6;
		background-color: lighten(#b9d5cd, 10%);
		.not-thumb {
			&:before {
				color: #7abfa6;
			}
		}
	}
	.datetime {
		color: #454545;
	}
}
@media (max-width: $screen-tab - 1) {
	.front-blog {
		background-color: #fff;
		box-shadow: 0 2px 2px 0 rgba(#333, .2);
	}
}
// Catalog ---*/
.front-catalog-wrap {
	.btn-catalog-info {
		color: #fff;
		background-color: #7abfa6;
	}
	.overlay {
		background-color: rgba(#fff, .6);
		&:before {
			content: "";
			position: absolute;
			right: 0;
			bottom: 0;
			display: block;
			width: 0;
			height: 0;
			border: 15px solid transparent;
			border-right-color: $default-color;
			border-bottom-color: $default-color;
		}
	}
	.title {
		color: #222;
	}
	.stylist {
		color: #222;
	}
	.swiper-slide {
		background: #f0f0f0;
	}

	$border-width: 2px;
	@media (min-width: $screen-tab + 1) {
		.swiper-button-prev,
		.swiper-button-next {
			&:before {
				border-top: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-prev {
			&:before {
				border-left: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-next {
			&:before {
				border-right: $border-width solid $swiper-border-color;
			}
		}
	}
}


// Front Social ---*/
#instagramJson {
	.nowloading {
