.btn {
	transition: all 100ms ease-in;
	&:hover {
		opacity: .8;
	}
}

// Color ---*/
.btn {
	// Default Color
	&-default {
		color: $btn-default-color;
		background-color: $btn-default-bg-color;
		background-repeat: repeat;
		background-position: center;
		// background-image: url("./common/_organic/images/common/bg_02.png");
		// border-color: $btn-default-border-color;
		border-color: transparent;
	}
	// Primary Color
	&-primary {
		color: $btn-primary-color;
		background-color: $btn-primary-bg-color;
		border-color: $btn-primary-border-color;
	}
	// Secondary Color
	&-secondary {
		color: $btn-secondary-color;
		background-color: $btn-secondary-bg-color;
		border-color: $btn-secondary-border-color;
	}
	// Shark Color
	&-shark {
		color: $btn-shark-color;
		background-color: $btn-shark-bg-color;
		border-color: $btn-shark-border-color;
	}
	// Ace Color
	&-ace {
		color: $btn-ace-color;
		background-color: $btn-ace-bg-color;
		border-color: $btn-ace-border-color;
	}
	// Radius
	&-radius {
		border-radius: 100%;
	}
}

// Size ---*/
.btn {
	// XL
	&-xl {
		font-size: 112%;
		padding: 16px 22px;
	}
	&-ll {
		padding: 12px 20px;
	}
	&-lm {
		padding: 10px 18px;
	}
	&-ls {
		padding: 8px 16px;
	}
	&-mid {
		font-size: 100%;
		padding: 6px 12px;
	}
	&-sl {
		font-size: 96%;
		padding: 6px 8px;
	}
	&-sm {
		font-size: 88%;
		padding: 5px 8px;
	}
	&-ss {
		font-size: 80%;
		padding: 4px 6px;
	}
}

.btn {
	.fa {
		margin-left: 4px;
		margin-right: 4px;
		font-size: 108%;
	}
}

// Catalog Carousel ---*/
.btn-catalog-info {
	position: absolute;
	top: 8px;
	left: 8px;
	z-index: 2;
	width: 42px;
	height: 42px;
	color: #fff;
	background-color: rgba(#f38, 1);
	outline: none;
	border-radius: 100%;
	box-shadow: 0 1px 2px 0 rgba(#222, .3);
	transition: all 160ms ease-in;
	&:focus {
		outline: none;
	}
	&:hover {
		background-color: rgba(#f38, .8);
	}
}
