.topics-archive {
	border-bottom: 1px solid #ddd;
}
.topics-header {
	.title {
		a {
			color: #1a1a1a;
		}
	}
}
.topics-content {
	.datetime {
		font-family: 'Amatic SC', serif;
		font-size: 24px;
	}
	@media (max-width: $screen-tab - 1) {
		.content {
			&:after {
				background: transparent;
			}
		}
	}
}
