.stylist-category {
	background-color: #7abfa6;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_free.png'));
	border: 1px solid darken(#7abfa6, 4%);
	.title {
		color: #525050;
		border-bottom: 1px solid darken(#7abfa6, 4%);
		&:hover {
			.toggle {
				// color: #fff;
			}
		}
	}
}

// Catalog Search ---*/
.catalog-search-wrapper {
	background-color: #fff;
}
.catalog_search {
	.cr {
		label {
			background-color: #fff;
		}
		input {
			&:checked {
				& + label {
					color: #fff;
					background-color: #68b499;
				}
			}
		}

	}
}

// Catalog LoopContent ---*/
.catalog-loop {
	.item {
		background-color: #fff;
		box-shadow: 0 1px 3px 0px rgba(#333, .2);
		transition: all 100ms ease-in;
		&:hover {
			opacity: .8;
		}
		.not-thumb {
			background-color: rgba(#fafafa, .3);
		}
		.border-catalog {
			border-top: 1px solid #bbb;
		}
		.style-name {
			a {
				color: #454545;
			}
		}
		.meta {
			.manage {
				span {
					color: #666;
					background-color: #f6f6f6;
					// border: 1px solid #999;
					border: none;
				}
			}
			.staff {
				// font-size: 16px;
				color: #555;
			}
		}
	}
}


// Single ---*/
.catalog-picture {
	&-body {
		.pic-wrap {
			background-color: #fafafa;
		}
	}
	&-list {
		&-item {
			&.active {
				border-color: $default-color;
			}
		}
	}
	.not-picture {
		color: rgba(#fff, .3);
		background-color: rgba($default-color, .8);
	}
}

// Catalog Content ---*/
.catalog-header {
	.title {
		border-bottom: 1px solid #ddd;
		&:before {
			content: url(dir('common/tlt_before.png'));
			display: inline-block;
			margin-right: 10px;
			vertical-align: middle;
		}
	}
	.comment {
		color: #454545;
		background-color: #f5f5f5;
		background-repeat: repeat;
		background-position: center;
		background-image: url(dir('common/bg_bread.png'));
		&:after {
			border-left: 14px solid #f5f5f5;
		}
	}
	.staff-pic {
		.thumb {
			background-color: rgba(darken(#f5f5f5, 2%), .3);
		}
	}
}

.catalog-table {
	th,td {
		border-bottom: 1px solid #ddd;
	}
	th {
		color: #888;
	}
}
.catalog-tag {
	& > li {
		a {
			color: #888;
			background-color: #fcfcfc;
			border: 1px solid #dedede;
		}
	}
}
.catalog-price {
	.price {
		font-family: $font-google;
		color: #888;
		.txt {
			font-size: 28px;
		}
		.slash {
			font-size: 20px;
		}
		.yen {
			font-size: 20px;
		}
		.numeric {
			font-size: 38px;
			color: #454545;
		}
		.kara {
			display: inline-block;
			vertical-align: middle;
			line-height: 1;
		}
	}
}

.catalog-footer {
	.other-title {
		font-family: $font-google;
		font-size: 32px;
		color: #010101;
		border-bottom: 1px solid #ddd;
	}
}
.other-catalog {
	.swiper-slide {
		a {
			background-color: rgba(#f8f8f8, .3);
		}
		.not-thumb {
			background-color: rgba(#f8f8f8, .3);
		}
	}
}


