/* Archive Pager */
.pagination {
	a {
		color: #888;
		background-color: #fff;
		border: 1px solid #ddd;
		border-radius: 100%;
		&:hover {
			color: #fff;
			background-color: #74bba2;
			border-color: #74bba2;
			opacity: .8;
		}
	}
	.inactive, .current {
		vertical-align: middle;
	}
	.inactive {
		width: 38px;
		height: 38px;
		line-height: 38px;
		font-size: 15px;
	}
	.current {
		width: 38px;
		height: 38px;
		font-size: 15px;
		line-height: 38px;
		color: #fff;
		background-color: #74bba2;
		border-color: #74bba2;
		border-radius: 100%;
	}
	.prev,.next {
		width: auto;
		height: 34px;
		line-height: 34px;
		vertical-align: middle;
		background-color: #fff;
		border: 1px solid #ddd;
		border-radius: 4px;
		&.not {
			color: #e8e8e8;
			text-shadow: 0 -1px 0 #d6d6d6;
			background-color: #f8f8f8;
			border: 1px solid #d6d6d6;
		}
		@media (min-width: $screen-tab) {
			padding-left: 24px;
			padding-right: 24px;
		}
	}
}

/* Single Pager */
.single-pager {
	ul {
		list-style: none;
		& > li {
			background-color: #ccc;
			a {
				color: #555;
			}
		}
	}
}

// Page Pager ---*/
.page-pager {
	& > span {
		color: $btn-default-color;
		background-color: $btn-default-bg-color;
	}
	& > a {
		background-color: #ccc;
		&:hover {
			color: $btn-default-color;
			background-color: $btn-default-bg-color;
		}
	}
}

// Link Pages ---*/
.link-pages-wrapper {
	& > span {
		color: $btn-default-color;
		background-color: $btn-default-bg-color;
	}
	& > a {
		color: #454545;
		background-color: #f0f0f0;
		border: 1px solid #ddd;
		&:hover {
			color: $btn-default-color;
			background-color: $btn-default-bg-color;
			border-color: $btn-default-border-color;
		}
	}
}
